/**
 * 
 * *
 * @param {*} fn // 
 * @param {*} delay // 
 */
export function debounce (fn:any, delay=400) {
  let timer:any = null;

  return function(this:any) {
    let args = arguments; // 
    let context = this; // 

    if (timer) { // 
      clearTimeout(timer);
    }
    
    timer = setTimeout(function () {
      fn.apply(context, args);
    }, delay);
  }
}

/**
 * 
 * @param value 
 */
export function isEmpty (value:any){
  return (
    value === undefined
    || null
    || (typeof value === 'object' && Object.keys(value).length === 0)
    || (typeof value === 'string' && value.trim().length === 0)
  )
}

/**
 * 
 */
export function getArrayObjIndexByKey(array:any,keyObj:any){
  let newIndex = -1

  const key = Object.keys(keyObj)[0]
  const val = keyObj[key]
  array.some((item:any,index:number)=>{
    if(item[key] == val){
      newIndex = index;
      return true;
    }
  })
  return newIndex
}